body {
  color: #172b4d;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Noto Sans,Ubuntu,Droid Sans,Helvetica Neue,sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  padding: 0;
  margin: 0;
}

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(0,0,0,.15);
  box-sizing: border-box;
  height: 40px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
}

.logo {
  height: 24px;
  opacity: 0.7;
}

.board {
  background-color: tomato;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1rem;
  height: 100vh;
  box-sizing: border-box;
  overflow-x: auto;
  overflow-y: hidden;
  padding-top: 55px;
  position: relative;
}
.board::after {
  color: rgba(0,0,0,.5);
  content: 'Â© 2020 alfieindesigns.com';
  position: absolute;
  display: block;
  width: 100%;
  text-align: center;
  bottom: 0;
  left: 0;
  font-size: 12px;
  line-height: 12px;
  padding: 1rem;
  box-sizing: border-box;
}

.list {
  background-color: #ebecf0;
  border-radius: 3px;
  flex: 0 1 272px;
  margin: 0 4px;
  box-sizing: border-box;
  padding: 8px 5px 0;
}

.list-card.dragging {
  position: relative;
  z-index: 1000;
}
.list-card {
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 1px 0 rgba(9,30,66,.25);
  padding: 6px 8px;
  position: relative;
  cursor: pointer;
  display: flex;
  margin-bottom: 8px;
  min-height: 20px;
  position: relative;
}
.list-card:hover {
  background-color: #f4f5f7;
}

.list-card .list-card-task {
  flex: 1 1 auto;
}
.list-card input {
  background-color: transparent;
  border: none;
  border-radius: 5px;
  padding: 0;
  flex: 1 1 auto;
}

input:focus {
  outline: none;
}

.btn-delete-card {
  position: absolute;
  opacity: 0;
  top: 5px;
  right: 5px;
  outline: none;
  border: none;
  padding: 0;
  height: 22px;
  width: 22px;
  font-size: 16px;
  line-height: 16px;
  color: #6b778c;
  border-radius: 3px;
  text-align: center;
  display: block;
  cursor: pointer;
  transition: all 250ms ease;
}
.btn-delete-card:hover {
  color: #172b4d;
  background-color: rgba(9,30,66,.08);
}

.list-card:hover .btn-delete-card {
  opacity: 1;
}

.list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 32px;
  margin-bottom:8px;
}

.list-header h2 {
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
  padding: 4px 8px;
  margin: 0;
  cursor: pointer;
  flex: 1 1 auto;
}

.list-header input {
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 0 8px;
  flex: 1 1 auto;
  min-height: 32px;
}

.action {
  outline: none;
  border: none;
  padding: 0;
  height: 32px;
  font-size: 16px;
  line-height: 16px;
  width: 32px;
  color: #6b778c;
  border-radius: 3px;
  text-align: center;
  display: block;
  cursor: pointer;
  transition: all 250ms ease;
}
.action:hover {
  color: #172b4d;
  background-color: rgba(9,30,66,.08);
}

.list-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom:8px;
}

/* Button */
.btn-add-card {
  outline: none;
  border: none;
  font-size: 14px;
  line-height: 14px;
  border-radius: 3px;
  color: #5e6c84;
  display: block;
  text-align: center;
  cursor: pointer;
  transition: all 250ms ease;
  padding: 8px;
  flex: 1 1 auto;
  text-align: left;
}
.btn-add-card:hover {
  color: #172b4d;
  background-color: rgba(9,30,66,.08);
}

.btn-add-list {
  background-color: hsla(0,0%,100%,.24);
  outline: none;
  border: none;
  font-size: 14px;
  line-height: 14px;
  border-radius: 3px;
  color: #f5f5f5;
  display: block;
  text-align: center;
  cursor: pointer;
  transition: all 250ms ease;
  padding: 12px;
  flex: 0 1 272px;
  margin: 0 4px;
  text-align: left;
}
.btn-add-list:hover {
  opacity: 1;
  color: #ffff;
  background-color: hsla(0,0%,100%,.32);
}